.Mywork {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  padding: 0px 50px;
  /* font-family: 'Spline Sans Mono', monospace;
    font-weight: lighter; */

}
.Mywork .mywork_top {
  width: 100%;
  height: auto;
  text-align: left;
}
.Mywork .mywork_left {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Mywork .mywork_left .mywork_left_inner {
  display: flex;
  flex-wrap: wrap;
  row-gap: 30px;
  justify-content: start;
  column-gap: 40px;
}
/* .Mywork .mywork_top_inner {
  text-transform: uppercase;
  padding: 40px 0px;
} */
.Mywork .mywork_top_inner h2 {
  text-transform: uppercase;
  font-size: 30px;
  padding: 40px 0px;
}

@media (max-width: 800px) {
    .Mywork{
        padding: 0px 30px;
        margin-top: 50px;
    }
  .Mywork .mywork_top {
    width: 100%;
    height: 60px;
    text-align: start;
  }
  .Mywork .mywork_top_inner h2 {
    font-size: 28px;
}
  .Mywork .mywork_left .mywork_left_inner {
    display: flex;
    flex-wrap: wrap; 
    row-gap: 30px;
    justify-content: center;
    column-gap: 40px;
    margin-top: 80px;
  }
}
@media (max-width: 800px) {
    .Mywork{
        padding: 0px 30px;
        margin-top: 30px;
    }

}
