
.Home{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    margin: 0px !important;
    background-color:#f8f9fa;
    overflow-y: hidden;
    max-height: 900px;
}
.Home .home_nav_div{
    width: 100%;
    height: 0px;
    /* background-color: rgba(65, 49, 49, 0.07); */
}
.Home .home_text_div{
    width: 60%;
    height: auto;
    /* margin: auto; */
}
.Home .home_img_div{
    width: 40%;
    height: auto;
    display: flex;
    justify-content: start;
    align-items: center;
}
.Home .home_img_div .home_img_inner_div{
    
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end; 
}
.Home .home_img_div img{
    width: 380px;
    height: auto !important;
    
}
.Home .home_text_div {
    width: 60%;
    height: auto;
    /* background-color: rgb(175, 116, 27); */
    display: flex;
    align-items: center;
    justify-content: center;
}
.Home .home_text_div .home_text_inner_div{
    
    width: auto;
    height: auto;
    display: flex;
    flex-direction: column;

}



/* //home_text_div */
.home_text_div span{
    font-size: 14px;
    font-weight: lighter;
    text-transform: uppercase;
    margin: auto 0;
}
.home_text_div h2{
    font-size: 50px;
    margin: 5px 0px;
    font-weight: bold;
    text-transform: uppercase;
    /* color: #6c757d; */
    position: relative;
    
}

.home_text_div p{
    font-size: 16px;
    margin: 5px 0px;
    font-weight: lighter;
    text-transform: uppercase;
}
.home_text_div .home_text_inner_div> a{
    font-size: 16px;
    margin: 20px 0px 0px 0px !important;
    font-weight: normal;
    text-transform: uppercase;
    border: none; 
    text-align: center;
    display: block;
    width: 150px;
    outline-style: none;
    background-color: none;
    padding: 10px 15px;
    background-color: #6c757d;
    border-radius: 3px;
    color: #f8f9fa !important;
    text-decoration: none;
}

.Home ul{
    display: flex;
    list-style: none;
    /* column-gap: 30px; */
    justify-content: flex-start;
    padding-left: 0px !important;
}
.Home ul a{
    width: 40px !important;
    height: 40px;
    border-radius: 50% !important;
    border: 2px solid #6c757d !important;
    display: flex;
    justify-content: center !important;
    text-align: center !important;
    align-items: center;
        margin-top: 10px !important;
    margin-right: 20px;
}
.Home ul a:hover{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color:#6c757d;
    transition: all 0.4s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
}
.Home ul a:hover *{
    color: #f8f9fa !important;
}





@media (max-width:1100px) {
    

    .Home .home_text_div{
        width: 65%;
    }
    .Home .home_img_div{
        width: 35%;
    }
    .Home .home_img_div img{
        width: 300px;
    }

}
@media (max-width:992px) {
    
    .Home{
        align-items: flex-start;
        padding: 0px 40px;
    }
    .Home .home_text_div{
        width: 100%;
        height: 100%;
    }
    .Home .home_img_div{
        display: none;
    }
    .home_text_div .home_text_inner_div{
        text-align: center;
        justify-content: space-between;
        height: 100%;
    }
    /* .home_text_div .home_text_inner_div{
        text-align: center;
        justify-content: center !important;
    } */
    .home_text_div .home_text_inner_div ul{
        justify-content: center;
        text-align: center;
    }
     .home_text_div .home_text_inner_div >a{
        text-align: center;
        margin: auto !important;
        margin-top: 20px !important;
    }

}

@media (max-width:600px) {
    .Home{
        padding: 0px 30px;
        width: 100vw !important;
        max-height: 700px !important;
    }
    .home_text_div span{
    font-size: 10px;
    font-weight: lighter;
    text-transform: uppercase;
    margin: 15px 0;
}
.home_text_div h2{
    font-size: 35px;
    margin: 10px 0px;
    font-weight: bold;
    text-transform: uppercase;
    /* color: #6c757d; */
    position: relative;
    
}

.home_text_div p{
    font-size: 15px;
    margin: 10px 0px;
    font-weight: lighter;
    text-transform: uppercase;
}
.Home .home_nav_div{
    display: none;
}
.Home .home_img_inner_div > a{
    margin-top: 20px !important;
}



}