.Contact{
    width: 100%;
    height: 100vh;
    max-height: 600px;
    display: flex;
    flex-wrap: wrap;
    padding: 50px 50px;
    justify-content: center;
    align-items: center;

}
.color_dark{
    color: #212121;
}
.bg_dark{
    background-color: #212121;
}
.Contact .contact_left{
    width: 50%;
}
.Contact .contact_right{
    width: 40%;
}


/* contact left */ 
.Contact .contact_left .contact_left_inner{
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
}
.Contact .contact_left .contact_left_inner h2{
    margin-bottom: 30px;
    /* color: #212121; */
    font-size: 30px;
}
.Contact .contact_left .contact_left_inner .contact_item{
    display: flex;
    justify-content: flex-start;
    padding: 20px 0px;
    width: 100%;
    align-items: center;
    /* font-family: 'Spline Sans Mono', monospace;  */
    font-weight: lighter    ;
    /* flex-direction: column; */
}
.Contact .contact_left .contact_left_inner .contact_item> img{
    width: 40px;
    height: 40px !important;

}
.Contact .contact_left .contact_left_inner .contact_item> span{
    font-size: 18px;
    margin-left: 50px;
    font-weight: normal;
}


/* contact right */
.Contact .contact_right .contact_right_inner{
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-direction: column;
    row-gap: 30px;
    width: 100%;
}
.Contact .contact_right .contact_right_inner .MuiFilledInput-root{
   padding: 0px 2px !important;
}




@media (max-width:992px) {
    .Contact{
    width: 100%;
    height: 100%;
    max-height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 0px 50px;
    justify-content: center;
    align-items: center;

}

.Contact .contact_left{
    width: 100%;
}
.Contact .contact_right{
    width: 80%;
    margin: 50px 0px;
}

.Contact .contact_left .contact_left_inner .contact_item{
   /* width: 100%;
   max-width: 400px;
   min-width: 300px; */

}

.Contact .contact_left .contact_left_inner{
    margin-bottom: 30px;
    /* color: #212121; */
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}
.Contact .contact_left .contact_left_inner h2{
   text-align: start !important;
   margin-right: auto;
}

}

@media (max-width:600px) {
    .Contact .contact_right{
    width: 100%;
    margin: 50px 0px;
}
}
@media (max-width:450px) {
    .Contact{
    width: 100%;
    margin: 20px 0px;
    padding: 0px 20px;
}
.Contact .contact_left .contact_left_inner h2{
    font-size: 28px;
}
.Contact .contact_left .contact_left_inner .contact_item > span{
    margin-left: 30px;
    font-size: 16px;
}
}