:root{
    --dark:#212121;
}
.Plateforms{
    width: 100%;
    /* height: 100vh; */
    /* max-height: 700px; */
    height: auto;
    /* padding: 50px 50px; */
    display: flex;
    flex-wrap: wrap;
    padding: 60px 80px;
    align-items: center;
}
.Plateforms .plateforms_left{
    width: 50%;
    display: flex;
    justify-content: start;
    align-items: flex-start;
}
.Plateforms .plateforms_right{
    width: 50%;
    display: flex;
    justify-content: start;
    align-items: flex-start;
}

/* left side  */

.Plateforms .plateforms_left .plateforms_left_inner{
    width: 100%;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: column;
    margin-right: 30px;
}
.Plateforms .plateforms_left .plateforms_left_inner h3{
    text-transform: uppercase;
    margin-bottom: 30px;
    /* color: var(--dark); */
}
.Plateforms .plateforms_left .plateforms_left_inner ul{
    /* list-style: none; */
    margin-left: 0px !important;
    padding-left: 0px !important;
}
.Plateforms .plateforms_left .plateforms_left_inner ul li{
    margin-bottom: 15px;
    font-size: 18px;
    font-weight: normal;
}
.Plateforms .plateforms_left_inner h3{
    font-size: 23px;
    margin-bottom: 30px;
}
.Plateforms .plateforms_left_inner ul li{
    font-size: 18px;
    
}

/* right side  */
.Plateforms .plateforms_right_inner h3{
    font-size: 23px;
    margin-bottom: 30px;
}
.Plateforms .plateforms_right .plateforms_cards{
    display: flex;
    flex-wrap: wrap;
    row-gap: 20px;
}
.Plateforms .plateforms_right .plateforms_card{
    width: 100%;
    display: flex;
    height: 90px;
    background-color: #6c757d;
    justify-content: start;
    align-items: center;
    border-radius: 5px;
    padding: 0px 10px;
    color: #f8f9fa;
}
.Plateforms .plateforms_right .plateforms_card:hover{
   transform: translateX(20px);
   transition: all 0.6s ease-in-out;
}
.Plateforms .plateforms_right .plateforms_card .avatar_box{
    margin-right: 20px;
     /* width: 20%; */
}
.Plateforms .plateforms_right .plateforms_card .avatar_box .text_box{
   
    font-family: 'Spline Sans Mono', monospace;
    text-align: start;
}
.Plateforms .plateforms_right .plateforms_card .text_box h3{
    text-transform: uppercase;
    font-size: 20px;
    color: #f8f9fa;
    margin: 5px 0px;
}
.Plateforms .plateforms_right .plateforms_card .text_box p{
    text-transform: capitalize;
    font-size: 16px;
    color: #f8f9fa;
}




@media (max-width:992px) {
    .Plateforms{
    width: 100%;
    /* height: 100vh; */
    /* max-height: 700px; */
    /* height: auto;
    display: flex;
    flex-wrap: wrap;
    padding: 60px 50px;
    align-items: center; */
    flex-direction: column;
    padding-bottom: 20px;
}
.Plateforms .plateforms_left{
    display: flex;
    justify-content: start;
    align-items: flex-start;
    order: 2;
    width: 100%;
    margin: 50px 0px;

}
.Plateforms .plateforms_right{
    width: 100%;
    display: flex;
    justify-content: start;
    align-items: flex-start;
}
}
@media (max-width:550px) {
    .Plateforms{
    width: 100%;

    height: 100% !important;
    display: flex;
    flex-wrap: wrap;
    padding: 60px 20px 30px 20px;
    align-items: center;
    flex-direction: column;
}
.Plateforms .plateforms_left{
    display: flex;
    justify-content: start;
    align-items: flex-start;
    order: 2;
    width: 100%;
}
.Plateforms .plateforms_right{
    width: 100%;


}
.Plateforms .plateforms_left_inner  {
    margin-right: 0px !important;
    padding-right: 0px;
    padding-left: 20px;
    
}
.Plateforms .plateforms_left_inner ul  {
    margin-bottom: 0px;

}

.Plateforms .plateforms_right .plateforms_card{
    height: auto;

}



}