.Footer {
  width: 100%;
  height: auto;
  padding: 20px 50px;
    background-color: #6c757d;
    color: #f8f9fa;
}
/* top of the foter */
.Footer .footer_top{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.Footer .footer_top .footer_top_left{
    /* width: 50%; */
    display: flex;
    justify-content: flex-end;
    align-items: center !important;
    
}
.Footer .footer_top .footer_top_left p{
    margin: 0% 10px;
    color: #f8f9fa !important;
    text-decoration: none;
}
.Footer .footer_top .footer_top_right{
    width: 50%;
    list-style: none;
    display: flex;
    row-gap: 30px !important;
    justify-content: flex-end;
    align-items: center;
}
.Footer .footer_top .footer_top_right li{
   margin: 0px 10px;
}
.Footer .footer_top .footer_top_right a{
    text-decoration: underline;
    cursor: pointer;
    color: #f8f9fa;
    margin-bottom: 0px;
}


/* //bottom of the footer */
.Footer .footer_bottom{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.Footer .footer_bottom .footer_bottom_left span{
    color: #f8f9fa;
}
.Footer .footer_bottom .footer_bottom_right li{
   margin: 0px 10px;
}
.Footer .footer_bottom .footer_bottom_right{
    display: flex;
    list-style: none;
    text-decoration: underline;
    
}
.Footer .footer_bottom_right{
    margin-bottom: 0px;
}
.Footer .footer_bottom .footer_bottom_right a *{
    
    color: #f8f9fa;
}



@media (max-width:992px) {
    .Footer {
  padding: 20px 30px;
}
}
@media (max-width:750px) {
    .Footer {

}
.Footer .footer_top{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    
}

.Footer .footer_top .footer_top_right{
   margin-top: 40px;
   width: 100%;
   justify-content: center;
    
}
.Footer .footer_top .footer_top_right a{
   text-decoration: underline !important;
    
}






}

@media (max-width:600px) {
    .Footer {
  width: 100%;
  height: 100%;
  padding: 20px 30px;
    background-color: #6c757d;
    color: #f8f9fa;
}
    .Footer .footer_bottom{
    flex-direction: column;
    }
    .Footer .footer_bottom{
    flex-direction: column;
    }
    .Footer .footer_bottom .footer_bottom_left{
    padding: 30px 0px;
    }
    .Footer .footer_bottom .footer_bottom_right{
    padding-top: 30px;
    padding-left: 0%;
    padding-right: 0%;
    padding-bottom: 10px;
    }
    .Footer .footer_bottom{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.Footer .footer_top .footer_top_right{
   flex-wrap: wrap;
   padding-left: 0px;
   padding-bottom: 20px;
    
}

.Footer .footer_bottom .footer_bottom_left{
    padding: 0px;
    padding-top: 30px;

}



}
