.Skills{
    width: 100%;
    height: 100vh;
    max-height: 500px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 10px 60px 60px 60px;
}
.Skills .skills_left{
    width: 50%;
    height: auto;
}
.Skills .skills_right{
    width: 44%;
    height: auto;
}


/* left side  */
.Skills .skills_left{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 56%;
    height: 100%;
}
.Skills .skills_left .skills_left_inner{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    height: auto;
}
.Skills .skills_left h2{
    font-size: 30px;
}
.Skills .skills_left p{
    margin: 20px 0px;
    font-size: 18px;
    font-weight: normal;
}
.Skills .skills_left .skills_list{
    display: flex; 
    flex-wrap: wrap;
    row-gap: 10px;
    column-gap: 10px;
}
.Skills .skills_left .skills_list .skills_logo{
      margin: 10px 10px;
}
.Skills .skills_left .skills_list .skills_logo img{
    width: 50px;
    height: 50px;
}


/* //right side odf the? div */

.Skills .skills_right .skills_right_inner{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 50px;
}
.Skills .skills_right .skills_right_inner ul{
    width: 100%;
    height: auto;
    list-style: none;

}
.Skills .skills_right .skills_right_inner ul li{
    margin-top: 20px;

}

.Skills .skills_left .skills_list > *{
    font-size: 18px;
}


@media (max-width:1100px) {
    .Skills{
    width: 100%;
    height: 100%;
    max-height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 50px 60px 60px 60px;
}
.Skills .skills_left{
    width: 50%;
    height: auto;
}
.Skills .skills_right{
    width: 44%;
    height: auto;
}
}

@media (max-width:1000px) {
    .Skills{
    width: 100%;
    height: 100%;
    max-height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: column;
    padding: 50px 60px 80px 60px;
}
.Skills .skills_left{
    width: 100%;
    height: auto;
}
.Skills .skills_right{
    width: 100%;
    height: auto;
}
.Skills .skills_right .skills_right_inner{
   padding-left: 0px;
    padding-right: 0px;
    margin-top: 30px;
}
.Skills .skills_right .skills_right_inner ul{
   padding-left: 0px;
    padding-right: 0px;
}
}

@media (max-width:800px) {
    .Skills{
        padding: 0px 30px;
    }
    .Skills .skills_left h2{
    font-size: 28px;
}
.Skills .skills_left p{
    margin: 20px 0px;
    font-size: 16px;
    font-weight: normal;
}

.Skills .skills_left .skills_list > *{
    font-size: 16px;
}


}