.Who{
    width: 100%;
    height: 100vh;
    max-height: 600px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 0px 50px;
    margin: 0px;
    margin-top: 50px;
    box-sizing: border-box;
    align-items: center;
}
.Who .who_left{
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Who .who_right{
    width: 60%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.who_left .who_left_inner{
    width: auto;
    min-width: 200px;
}
.who_left .who_left_inner h3{
   font-size: 35px;
   margin: 0px 0px 30px 0px;
}
.who_left .who_left_inner img{
   width: 100%;
   max-width: 300px;
   border-radius: 4px;
}


/* // right inner div */
.who_right .who_right_inner{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    padding-right: 30px;
    flex-direction: column;
}
.who_right .who_right_inner h3{
    text-align: start;
    font-size: 30px;
    margin-bottom: 20px;
}
.who_right .who_right_inner p{
    font-size: 18px;
    /* font-family: 'Spline Sans Mono', monospace; */
    padding: 0px;
    line-height: 1.3;
    margin: 0px;
    font-weight: normal;
}


@media (max-width:1100px) {
    .Who{
        flex-direction: column !important;
        flex-wrap: nowrap;
        text-align: center;
        max-height: 100% !important;
        height: 100%;
    }
    .Who .who_left{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Who .who_right{
    width: 100%;
    text-align: center;
    margin: 40px 0px;
}

.who_right .who_right_inner h3{
   text-align: center !important;
   margin: 30px  auto;

}



}

@media (max-width:800px) {
    .Who{
        padding: 0px 30px;
    }
        .who_right .who_right_inner {
    padding-right: 0px;
    }
    .who_right .who_right_inner h3{
   text-align: start !important;
   margin-left: 0px;
   
}
    .who_right .who_right_inner p{
   text-align: start !important;
}
.who_right .who_right_inner p{
    font-size: 16px;
    
}
.who_right .who_right_inner h3{
    font-size: 28px;
}
}
@media (max-width:500px) {
    .Who{
        padding: 0px 20px;
        align-items: flex-start;
    }
}